@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

body,
h1,
h2,
h3,
h4,
p,
li {
  margin: 0;
}

html {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}

a {
  text-decoration: none;
  font-size: 2em;
}
a:visited {
  color: inherit;
}

i {
  color: #ffffff;
  transition: all 0.3s;
  font-size: 1.5em;
  pointer-events: none;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@font-face {
  font-family: Inter-Regular;
  src: url(./../font/Inter-Regular.ttf);
}
@font-face {
  font-family: Inter-Medium;
  src: url(./../font/Inter-Medium.ttf);
}
@font-face {
  font-family: Inter-Bold;
  src: url(./../font/Inter-Bold.ttf);
}
.main-font_regular {
  font-family: Inter-Regular, sans-serif;
}

.main-font_medium {
  font-family: Inter-Medium, sans-serif;
}

.main-font_bold {
  font-family: Inter-Bold, sans-serif;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #F1FAEE;
}

.header {
  width: 100%;
}
.header__background {
  background-color: #1D3557;
  text-align: center;
  padding: 22px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__title {
  display: block;
  font-size: 64px;
  color: #ffffff;
  padding: 0px 30px;
}
@media (max-width: 768px) {
  .header__title {
    font-size: 47px;
    line-height: 33px;
  }
}
@media (max-width: 576px) {
  .header__title {
    font-size: 30px;
    line-height: 33px;
  }
}

main {
  flex: 1 1 auto;
  width: 100%;
}

.main__container {
  height: 100%;
}

.footer {
  width: 100%;
}
.footer__background {
  background-color: #1D3557;
  text-align: center;
  padding: 10px;
}
.footer__copyright {
  font-size: 16px;
  color: #ffffff;
}

a.thanks {
  font-size: 12px;
  color: #ffffff;
}

a.thanks:visited {
  color: inherit;
}

.levels {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  justify-content: space-evenly;
}
.levels__description {
  font-size: 32px;
  line-height: 30px;
  color: #000000;
  margin: 25px 0 30px 0;
  text-align: center;
}
.levels__block {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  max-width: 1000px;
  margin-bottom: 30px;
}

.level {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 30px;
  background-color: #457B9D;
  max-width: 270px;
  max-height: 180px;
  cursor: pointer;
  user-select: none;
}
.level__name {
  font-size: 96px;
  color: #ffffff;
  min-height: 75px;
  margin: 20px 25px 0 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.level__name span {
  font-size: 56px;
  margin: 0 40px;
}
.level__stars {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 15px 0 10px 0;
}

.star-icon {
  display: block;
  width: 60px;
  height: 60px;
  fill: #000000;
  margin: 0 3px;
  transition: fill 0.8s;
}
.star-icon.yellow {
  fill: #FFE600;
}

div.hide-block {
  display: none;
}

.panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1025px;
  width: 100%;
  padding: 20px 30px;
}
@media (max-width: 768px) {
  .panel {
    flex-direction: column-reverse;
    gap: 20px;
  }
}
.panel p {
  font-size: 36px;
}
@media (max-width: 768px) {
  .panel p {
    font-size: 24px;
  }
}

.btn {
  font-size: 36px;
  border: 5px solid #1D3557;
  padding: 5px;
  border-radius: 30px;
  color: #1D3557;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  transition-property: color, background-color;
}
@media (max-width: 768px) {
  .btn {
    font-size: 24px;
  }
}
.btn:hover {
  background-color: #1D3557;
  color: #ffffff;
}
.btn:hover i {
  color: #F1FAEE;
}
.btn i {
  color: #1D3557;
}

.button_share {
  font-size: 27px;
  border-radius: 15px;
}

.button_goback {
  padding: 5px 42px;
}
.button_goback.blocked {
  cursor: not-allowed;
}
@media (max-width: 768px) {
  .button_goback {
    margin-bottom: 10px;
  }
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  justify-content: space-evenly;
}
@media (max-width: 768px) {
  .game {
    position: relative;
  }
}
.game__container {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  padding: 20px;
  max-width: 1300px;
  max-height: 900px;
  width: 100%;
  height: 100%;
}
@media (max-width: 768px) {
  .game__container {
    padding: 5px;
  }
}
.game__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
}
@media (max-width: 768px) {
  .game__wrapper {
    padding: 2px;
  }
}

.card {
  max-width: 210px;
  max-height: 130px;
  width: 100%;
  height: 100%;
  border-radius: 30px;
  position: relative;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  background-color: #457B9D;
  cursor: pointer;
  user-select: none;
}
@media (max-width: 992px) {
  .card {
    border-radius: 15px;
  }
}
@media (max-width: 576px) {
  .card {
    border-radius: 8px;
  }
}
.card__back, .card__front {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
@media (max-width: 768px) {
  .card__back, .card__front {
    padding: 2px;
  }
}
.card__front p {
  color: #ffffff;
  font-size: 96px;
}
@media (max-width: 1200px) {
  .card__front p {
    font-size: 64px;
  }
}
@media (max-width: 992px) {
  .card__front p {
    font-size: 52px;
  }
}
@media (max-width: 768px) {
  .card__front p {
    font-size: 36px;
  }
}
@media (max-width: 576px) {
  .card__front p {
    font-size: 24px;
  }
}
.card__back {
  transform: rotateY(180deg);
  overflow: hidden;
}
.card__back img {
  height: 100%;
  width: auto;
}
.card.animated .card__front p {
  opacity: 0;
  animation: 0.8s shown-card-after 0.8s;
}
.card.active {
  transform: rotateY(180deg);
}
.card.active .card__front p {
  visibility: hidden;
}

@keyframes shown-card-after {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(120, 120, 120, 0.7);
  display: grid;
  place-items: center;
  transition: var(--transition);
  visibility: hidden;
  z-index: -10;
}
.modal-overlay .modal {
  overflow: hidden;
  min-width: 300px;
  max-width: 600px;
  width: 50%;
  height: 550px;
  background-color: #F1FAEE;
  border-radius: 42px 42px 62px 62px;
  box-shadow: 1rem 1rem 1rem 1rem rgba(96, 121, 138, 0.6);
}
.modal-overlay .modal__header {
  display: flex;
  justify-content: center;
  padding: 20px;
  background-color: #1D3557;
  color: #ffffff;
}
@media (max-width: 768px) {
  .modal-overlay .modal__header {
    padding: 10px;
  }
}
.modal-overlay .modal__star {
  width: 100px;
  height: 100px;
}
.modal-overlay .modal__body {
  padding: 20px;
  border: 40px solid #1D3557;
  border-top: none;
  border-radius: 0 0 60px 60px;
  height: 410px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 768px) {
  .modal-overlay .modal__body {
    padding: 10px;
    border-width: 20px;
    height: 430px;
  }
}
.modal-overlay .modal__level {
  font-size: 40px;
  font-weight: 900;
  text-transform: uppercase;
  text-shadow: -1px -1px 0 #FFE600, 1px -1px 0 #FFE600, -1px 1px 0 #FFE600, 1px 1px 0 #FFE600;
}
.modal-overlay .modal__text {
  font-size: 30px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
}
.modal-overlay .modal__text span {
  display: block;
}
.modal-overlay .modal__stats {
  font-size: 30px;
  display: inline-flex;
  width: 100%;
  justify-content: space-between;
}
.modal-overlay .modal__btn {
  position: static;
}
.modal-overlay .modal__buttons {
  display: flex;
  flex-wrap: wrap-reverse;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.open-modal {
  visibility: visible;
  z-index: 10;
}